<template>
  <div class="g-merchantDetails">
    <div class="m-img">
      <img :src="'https://fx18.oss-cn-hangzhou.aliyuncs.com/'+bit.icon" alt="">
    </div>
    <div class="m-content">
      <div>公司名称:{{bit.name}}</div>
      <div>经营类目:{{bit.business||'无'}}</div>
      <div>店铺地址:{{bit.province}}{{bit.city}}{{bit.area}}{{bit.address}}</div>
      <div>联系人:{{bit.contact||'无'}}</div>
      <div>联系电话:{{bit.phone||'无'}}</div>
    </div>
    <div class="m-details">
      <div class="s-title">公司简介</div>
      <div class="s-content">{{bit.intro}}</div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "merchantDetails",
    data(){
      return {
        bit:'',
      }
    },
    created(){
      this.merchantD();
    },
    methods:{
      merchantD(){
        this.$post(this.$bee+'/api/shop/leagueShopDetail',{username:this.$route.query.username}).then((data)=>{
          if(data.code==200){
            this.bit=data.data.shop;
          }else{
            this.$refs.scroll.forceUpdate()
          }
        })
      }
    }
  }
</script>

<style scoped lang="stylus">
.m-img
  width 100%
  height 410px
  img
    width 100%
    height 100%
.m-content
  padding 15px 14px 13px 14px
  position relative
  &>div
    color: #535353;
    font-size: 13px;
    text-align left
    margin-bottom 5px
    padding 5px 0
    text-overflow:ellipsis;
    white-space:nowrap;
    &:nth-child(1)
      font-size 15px
    &:last-child
      margin 0
  &:after
    content ''
    position absolute
    bottom 0
    width: 350px;
    height: 1px;
    background-color: #d6d6d6;
    left 13px
.m-details
  padding 8px 14px 13px 14px
  .s-title
    font-size: 13px;
    color: #535353;
    font-weight bold
    text-align left
  .s-content
    text-align left
    margin-top 6px
    color: #535353;
    font-size: 11px;
    line-height 1.5
</style>
